@import 'scss/style.minxin.scss';

// common tablete
.MainContainer {
    @include Container();
    width: 100%;
    background: url('/resources//image/main_image.png') no-repeat center bottom/1368px;
}


// common mobile
@media screen and (max-width: 1620px) {
    .MainContainer {width: 100%; min-height: 100vh; padding-bottom: 0;
        background: url('/resources//image/main_image.png') no-repeat center bottom calc(100vw*(-20/1620)) /100%;}
}
