// tablete
.MainWrap {
    width: 952px; min-height: 100vh; height: 2100px;
    margin: 0 auto;

    @media screen and (max-width: 1620px) {
        width: calc(100vw*(278/390)); height: auto;
    }
}


.MainSection {
    position: relative;
    width: 100%;
    padding: 150px 0;

    @media screen and (max-width: 1620px) {
        padding: calc(100vw*(120/1620)) 0;
    }
}
.ColorBar {
    position: absolute; top: calc(100vw*(160/390)); left: 50%;
    width: calc(100vw*(12/390));
    margin-left: calc(100vw*(-180/390));
}
.Logo {
    width: 100%;
    margin-bottom: 70px;
    padding: 22px;

    @media screen and (max-width: 1620px) {
        margin-bottom: calc(100vw*(30/390));
        padding: calc(100vw*(22/1620));
    }
}
.StartSection {
    position: relative;
    width: 760px;
    margin: 540px auto 85px;
    img {width: 100%;}
    label {
        position: absolute; top: 53%; left: 0;
        width: 100%;
        text-align: center;
        font-size: 60px;
        font-family:'HelveticaNeue_bold';
        font-weight: 900;
        transform: translateY(-50%);
        cursor: pointer;
    }

    @media screen and (max-width: 1620px) {
        position: fixed; left: 50%; bottom: calc(100vw*(56/390));
        width: calc(100vw*(760/1620));
        transform: translateX(-50%);
        margin: 0;
        label {
            font-size: calc(100vw*(20/390));
        }
    }
}
.StartTextBox {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: auto;
    p {
        font-size: 30px;
        font-weight: 500;
        line-height: 1.42;
        padding: 22px;
        background-color: #ffffff;
    }

    @media screen and (max-width: 1620px) {
        p {
            width: 100%;
            font-size: calc(100vw*(14/390));
            text-align: center;
            padding: calc(100vw*(22/1620));
        }
    }
}

.BorderText {font-size: calc(100vw*(17/390)); font-family: 'HelveticaNeue_bold'; padding: calc(100vw*(8/390)) 0;}
.StartTextBox {font-size: calc(100vw*(17/390)); font-family: 'HelveticaNeue_bold'; margin-top: calc(100vw*(18/390));}