@import 'scss/style.minxin.scss';

.MainContainer {
    @include Container();
    width: 100%;
}

.MainWrap {
    width: 952px; min-height: 100vh; height: 2100px;
    margin: 0 auto;

    @media screen and (max-width: 1620px) {
        width: calc(100vw*(306/390)); height: auto;
    }
}


.MainSection {
    position: relative;
    width: 100%;
    padding: 150px 0;

    @media screen and (max-width: 1620px) {
        padding: calc(100vw*(120/1620)) 0;
    }
}
.ColorBar {
    position: absolute; top: calc(100vw*(160/390)); left: 50%;
    width: calc(100vw*(12/390));
    margin-left: calc(100vw*(-180/390));
}
.Logo {
    width: 100%;
    margin-bottom: 70px;
    padding: 22px;

    @media screen and (max-width: 1620px) {
        margin-bottom: calc(100vw*(20/390));
        padding: calc(100vw*(18/390));
    }
}
.StartSection {
    position: relative;
    width: 760px;
    margin: 540px auto 85px;
    img {width: 100%;}
    p {
        position: absolute; top: 53%; left: 0;
        width: 100%;
        text-align: center;
        font-size: 60px;
        font-family:'HelveticaNeue_bold';
        font-weight: 900;
        transform: translateY(-50%);
        cursor: pointer;
    }

    @media screen and (max-width: 1620px) {
        position: fixed; left: 50%; bottom: calc(100vw*(56/390));
        width: calc(100vw*(760/1620));
        transform: translateX(-50%);
        margin: 0;
        p {
            font-size: calc(100vw*(20/390));
        }
    }
}
.StartTextBox {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: auto;
    p {
        font-size: 30px;
        font-weight: 500;
        line-height: 1.42;
        padding: 22px;
        background-color: #ffffff;
    }

    @media screen and (max-width: 1620px) {
        p {
            width: 100%;
            font-size: calc(100vw*(14/390));
            text-align: center;
            padding: calc(100vw*(60/1620));
        }
    }
}
.BorderText {font-size: calc(100vw*(17/390)); font-family: 'HelveticaNeue_bold'; padding: calc(100vw*(8/390)) 0;}
.BtnBox {display: flex; align-items: center; justify-content: center; gap: calc(100vw*(24/390)); width: 100%; margin-top: calc(100vw*(50/390));}
.ImgBox {
    display: flex; align-items: center; justify-content: center;
    width: 100%; border: 1px solid #000000;
    padding: calc(100vw*(20/390)); margin-top: calc(100vw*(30/390)); background: #fff;
    .ImgInner {
        position: relative;
        width: 100%; height: 100%;
        & img {width: 100%; height: 100%; object-fit: contain;}
    }
}
.ImgBox i {
    position: absolute; width: calc(100vw*(24/390)); height: calc(100vw*(24/390));
    &:nth-of-type(1) {
        top: calc(100vw*(10/390)); left: calc(100vw*(10/390));
        border-top: 1px solid #fff; border-left: 1px solid #fff;
    }
    &:nth-of-type(2) {
        top: calc(100vw*(10/390)); right: calc(100vw*(10/390));
        border-top: 1px solid #fff; border-right: 1px solid #fff;
    }
    &:nth-of-type(3) {
        bottom: calc(100vw*(10/390)); left: calc(100vw*(10/390));
        border-bottom: 1px solid #fff; border-left: 1px solid #fff;
    }
    &:nth-of-type(4) {
        bottom: calc(100vw*(10/390)); right: calc(100vw*(10/390));
        border-bottom: 1px solid #fff; border-right: 1px solid #fff;
    }
}